.card_container {
  max-width: 33.1%;
  width: 100%;
  max-height: 400px;
  height: 100%;
  border-radius: 30px;
  margin: 2px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: linear-gradient(225deg, rgb(113, 118, 109), rgb(246, 246, 246) 44%, rgb(239, 247, 110));
}

#card_img {
  width: 50%;
  image-rendering: pixelated;

  &:hover {
    transform: scale(1.1);
  }
}

.formatted_name {
  text-transform: capitalize;
}